import { getUrl } from '@/router';
import { useScannerFrame } from './scannerFrame';
import { defineComponent, computed, ref } from 'vue';
export default defineComponent({
  props: {
    showScanner: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:showScanner'],

  setup(props, {
    emit
  }) {
    const frameSrc = getUrl('frames.achievement.scan');
    const scannerFrame = ref(null);
    const scannerResult = ref({
      show: false,
      length: 0,
      faildImages: []
    });
    const showScanner = computed({
      get() {
        return props.showScanner;
      },

      set(value) {
        emit('update:showScanner', value);
      }

    });
    const {
      sendOops
    } = useScannerFrame({
      scannerFrame: scannerFrame,
      results: scannerResult,
      showScanner
    });
    return {
      frameSrc,
      sendOops,
      scannerFrame,
      scannerResult
    };
  }

});