import "core-js/modules/es.array.includes.js";
import { i18n } from '@/i18n';
import { defineComponent, toRef, computed } from 'vue';
import 'element-plus/theme-chalk/el-link.css';
export default defineComponent({
  props: {
    achievement: {
      type: Object,
      required: false
    }
  },
  emits: ['close'],

  setup(props) {
    const triggerText = {
      FINISH_QUEST_OR: '',
      FINISH_QUEST_AND: '',
      FINISH_PARENT_QUEST_OR: '',
      FINISH_PARENT_QUEST_AND: '',
      DAILY_TASK_VAR_EQUAL: ''
    };
    const taskType = {
      WQ: '',
      AQ: '',
      IQ: '',
      LQ: '',
      H: ''
    };
    const tasks = computed(() => {
      if (!props.achievement || !props.achievement.trigger.task) return [];
      return props.achievement.trigger.task.map(e => {
        return { ...e,
          badges: e.type.split(' ')
        };
      });
    });
    const hasHiddenOperation = computed(() => {
      return tasks.value.find(e => e.badges.includes('H'));
    });
    const showNoTask = computed(() => {
      if (!props.achievement) return [];
      return tasks.value.length === 0 && ['FINISH_QUEST_OR', 'FINISH_QUEST_AND', 'FINISH_PARENT_QUEST_OR', 'FINISH_PARENT_QUEST_AND'].includes(props.achievement.trigger.type);
    });
    return {
      triggerText,
      taskType,
      amos: toRef(i18n, 'amos'),
      tasks,
      hasHiddenOperation,
      showNoTask
    };
  }

});