import "core-js/modules/es.array.includes.js";
import { i18n } from '@/i18n';
import versionMap from './versionMap';
import img from '@/assets/images';
import { toRef, defineComponent, computed } from 'vue';
import { UIAFStatus } from '@/typings/Achievement';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faArrowUpRightFromSquare } from '@fortawesome/free-solid-svg-icons';
import dayjs from 'dayjs';
library.add(faArrowUpRightFromSquare);
export default defineComponent({
  props: {
    i: {
      type: Object,
      required: true
    },
    fin: {
      type: Object,
      required: false
    },
    preFin: {
      type: Object,
      required: false
    },
    contributed: {
      type: Object,
      required: true
    },
    partial: {
      type: Array,
      required: true
    }
  },
  emits: ['input-date', 'input-current', 'input-partial', 'check', 'click-title'],

  setup(props, {
    emit
  }) {
    const badgeMap = {
      WQ: '',
      IQ: '',
      AQ: '',
      LQ: ''
    };

    const searchMys = i => {
      return `https://www.miyoushe.com/ys/search?keyword=${encodeURIComponent(i18n.amos[i.name])}`;
    };

    return {
      img,
      searchMys,
      amos: toRef(i18n, 'amos'),
      questType: computed(() => {
        if (props.i.trigger && props.i.trigger.task && props.i.trigger.task.length > 0) {
          return badgeMap[props.i.trigger.task[0].type] ? [badgeMap[props.i.trigger.task[0].type], props.i.trigger.task[0].type] : false;
        } else {
          return false;
        }
      }),
      isFin: computed(() => {
        return props.fin && props.fin.status > UIAFStatus.ACHIEVEMENT_UNFINISHED;
      }),
      version: computed(() => {
        if (!versionMap[props.i.id]) {
          console.log(props.i.id, 'has no version');
          return 'BETA';
        }

        return versionMap[props.i.id].toFixed(1) || '';
      }),

      formatDate(datestr) {
        try {
          const d = new Date(datestr * 1000); // check 0

          if (d.getTime() === 0) {
            return '';
          } // check has hour minute second


          if (d.getHours() || d.getMinutes() || d.getSeconds()) {
            return dayjs(d).format('YYYY/MM/DD HH:mm:ss');
          }

          return dayjs(d).format('YYYY/MM/DD');
        } catch (e) {
          return '';
        }
      },

      updateDate(datestr) {
        const d = datestr.trim();

        if (d === '') {
          emit('input-date', 0);
          return;
        }

        try {
          const dt = new Date(d);

          if (!d.includes(':') && dt.getHours() === 8 && dt.getMinutes() === 0 && dt.getSeconds() === 0) {
            dt.setHours(0);
          }

          emit('input-date', Math.floor(dt.getTime() / 1000));
        } catch (e) {}
      },

      partialTypeMap: {
        quest: '',
        subquest: '',
        task: '',
        subtask: '',
        achievement: ''
      },
      partialClassMap: {
        quest: 'WQ',
        subquest: 'WQ',
        task: 'IQ',
        subtask: 'IQ',
        achievement: 'AC'
      },

      getPartialDate(fin, id) {
        if (!fin) return '';
        const ts = fin.partial[id];
        if (!ts) return '';
        return dayjs(ts * 1000).format('YYYY/MM/DD HH:mm:ss');
      },

      getPartialName(namearr) {
        const nameArr = namearr.map(i => {
          if (typeof i === 'number') {
            return i18n.amos[i] || i.toString();
          } else {
            return i;
          }
        });
        nameArr.forEach((i, a) => {
          if (i.indexOf('/') === 0) {
            const regex = new RegExp(i.split('/')[1], i.split('/')[2]); // remove regex

            nameArr[a] = ''; // match last item

            const matches = nameArr[a - 1].match(regex);

            if (matches && matches[1]) {
              nameArr[a - 1] = matches[1];
            }
          }
        });
        return nameArr.join('');
      }

    };
  }

});